import React, { useState,useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import ClearIcon from "@material-ui/icons/Clear";
import Button from "@material-ui/core/Button";

import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import Checkbox from "@material-ui/core/Checkbox";

import InputBase from "@material-ui/core/InputBase";
import SearchIcon from "@material-ui/icons/Search";
import { IconButton} from "@material-ui/core";
import styles from '../../../UI/css/ReportModal.module.css';
import axios from "../../../axios";
import {useSelector} from "../../../Redux";
import { useTranslation } from 'react-i18next';

import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import { GetStateFromData } from "../../../components/CustomHooks/CustomHooks";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    maxWidth: "100%",
    backgroundColor: theme.palette.background.paper,
    position: "relative",
    overflow: "auto",
    maxHeight: "252px",
    paddingBottom: 0,
    margin: 0,
  },
  paper: {
    position: "absolute",
    width: "793px",
    height: "437px",
    top: "50%",
    left: "50%",
    transform: `translate(-50%, -50%)`,
    background: "#FFFFFF 0% 0% no-repeat padding-box",
    boxShadow: "0px 0px 10px #51515180",
    opacity: "1",
  },
  search: {
    position: "relative",
    borderRadius: theme.shape.borderRadius,

    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      marginLeft: theme.spacing(3),
      width: "auto",
    },
  },
  searchIcon0: {
    padding: theme.spacing(0, 2),
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  inputBase: {
    fontSize: "0.8rem",  
    color: "#606060",
  },
  input2: {
        '&:focus':{
            outline: 'none !important',
            boxShadow: 'none !important',
            border: 'none !important',
            WebkitBoxShadow: 'none !important'
        }
  },
  searchIcon: {
    padding: "1px",
    fontSize: "0.5rem",
  },
  searchIconSpan: {
    marginTop: "-5px"
  },
  listItemText :{
    color:"#000000",
    textAlign: "left",
    font: "normal normal normal 12px/12px Open Sans",
    alignItems: "center",
    display:"flex",
    margin: "0px",
    marginLeft: "5px",
    marginRight: "5px",
    letterSpacing: "0px",
    opacity: "1",
    "& .MuiTypography-body1": {
        font: "normal normal normal 12px/12px Open Sans",
    }
  },
  menu: {
    padding: "0px"
  },
  menuItem: {
    padding: "0px",
    margin: "0px",
    // height: "30px"
  },
  listItemText2: {
    padding: "0px",
    margin: "0px",
    minWidth: "40px",
    font: "normal normal normal 12px/12px Open Sans",
    "& .MuiTypography-body1": {
        font: "normal normal normal 12px/12px Open Sans",
        overflow:" hidden !important",
        textOverflow: "ellipsis !important",
         whiteSpace: "nowrap !important",
         width: "100% !important",
        display: "inline-block !important"
    }
  },
  listItemIcon: {
    padding: "0px",
    margin: "0px",
    height: "28px",
    minWidth: "40px",
  },
  checkbox: {
    padding: "0px",
    margin: "0px",
    color:"#0072C6",
  },


  tableContainer: {
    overflow: "auto",
    display: "flex",
    flexDirection: "column",
    marginRight: "11px",
    marginLeft: "26px",
    maxHeight: "252px",
    // width: "98%",
    width: "751px",
    border: "1px solid #C4C4C4",
    borderRadius: "2px",
    background: "#FFFFFF 0% 0% no-repeat padding-box",
    verticalAlign: "middle",
    opacity: "1",
  },
  tableContainerOnMF: {
    overflow: "auto",
    display: "flex",
    flexDirection: "column",
    marginRight: "11px",
    marginLeft: "10px",
    maxHeight: "460px",
    width: "98%",
    border: "1px solid #C4C4C4",
  },
  table: {
    minWidth: "300px",
    flex: "auto",
  },
  tableHead: {
    opacity: 1,
    textAlign:
      window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
        ? "right"
        : "left",
    font: "normal normal 600 12px/17px OpenSans-Semibold",
    letterSpacing: "0px",
    height: "30px",
  },
  tableBody: {
    textAlign:
      window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
        ? "right"
        : "left",
    font: "normal normal 600 14px/19px OpenSans",
    letterSpacing: "0px",
    color: "#000000",
  },
  tableHeadRow: {
    height: "36px",
    background: "#F0F0F0 0% 0% no-repeat padding-box",
    font: "normal normal 600 12px/17px Open Sans",
  },
  tableBodyRow: {
    height: "36px",
    background: "#FFFFFF 0% 0% no-repeat padding-box",
    font: "normal normal normal 12px/17px Open Sans",
  },
  activeBodyRow: {
    height: "36px",
    background: "#0072C60F 0% 0% no-repeat padding-box",
    font: "normal normal normal 12px/17px Open Sans",
  },
  tableHeadCell: {
    font: "normal normal 600 12px/17px Open Sans",
    height: "auto",
    // width: "220px",
    padding: "0px 30px",
    color: "#606060",
    background: "#E0E0E0 0% 0% no-repeat padding-box",
    textAlign: window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA" ? "right" : "left",
  },
  tableBodyCell1: {
    textAlign: window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA" ? "right" : "left",
    font: "normal normal 600 12px/17px Open Sans",
    height: "auto",
    // width: "250px",
    padding: "0px 30px",
    color: "#000000",
    textOverflow:"ellipsis",
    overflow:"hidden",
    whiteSpace:"nowrap",
    maxWidth: "280px",
  },
  tableBodyCell2: {
    textAlign: window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA" ? "right" : "left",
    font: "normal normal 600 12px/17px Open Sans",
    height: "auto",
    // width: "220px",
    padding: "0px 30px",
    color: "#000000",
    textOverflow:"ellipsis",
    overflow:"hidden",
    whiteSpace:"nowrap",
    maxWidth: "380px",
  },
  foot: {
    width: "100%",
    height: "50px",
    color: "gray",
    position: "fixed",
    bottom: 0,
    background: "#F5F5F5 0% 0% no-repeat padding-box",
  },
  menuLi: {
    "& .MuiMenuItem-root.Mui-selected": {
        background: "#fff !important",
        color: "#000 !important",
    },
    "& .MuiMenuItem-root.Mui-selected:hover": {
        background: "#fff !important",
        color: "#000 !important",
    }
  }


}));


const SurveyModal = (props) => {
    const classes = useStyles();

    const { t } = useTranslation();
    const contexts = GetStateFromData(props.containerId);

    const {updateJson, openingAsApplication, formMataData} = contexts.contexts;
    const pid =contexts.pid;
    const isWorkItem =contexts.isWorkItem;
    
    const {updatedComponentInFormState} = updateJson;

    const [empty, setEmpty] = useState(false);

    let column = [t("Survey Name"), t("Category")];

    const [categories, setCategories] = useState([]);
    const [anchorEl, setAnchorEl] = useState(null);
    const [selected, setSelected] = useState([]);
    const [selectedIds, setSelectedIds] = useState([]);
    const [selectedNames, setSelectedNames] = useState([]);
    const [search, setSearch] = useState("%20");
    const [filter, setFilter] = useState("");
    const [activeArray, setActiveArray] = useState([]);
    const [selectedRowIndex, setSelectedRowIndex] = useState(0);
    const [selectedRow, setSelectedRow] = useState({});

    useEffect(()=>{
        axios.get(`/categories/survey/Name/ASC/%20`,{withCredentials:true}) 
            .then(res => {
                if(res.data){
                    setCategories(res.data);
                }
            })
            .catch(err=>{
                console.log("Error in survey..", err );
            })
    },[])


    useEffect(()=>{
        axios.post(`/survey/categories/categoriesId/${search}`,
        {
            "id": selectedIds
        },
        {withCredentials:true}) 
        .then(res => {
            if(res.data){
                setActiveArray(res.data);
                setSelectedRow(res.data[0]);
            }
        })
        .catch(err=>{
            console.log("Error in survey..", err );
        })
    },[search,selectedIds])

    function searchWord(){
        if(filter){
            setSearch(filter);
        } else {
            setSearch("%20");
        }
    }

  

    function handleSearch(e) {
        setFilter(e.target.value);
        if (e.key === 'Enter') 
        {
            searchWord();
        }
    }

    const handleClickListItem = (event) => {
        setAnchorEl(event.currentTarget);
    };
    
    const handleMenuItemClick = (event, index, item) => {

        if(selected.includes(index)){
            let a = selected;
            a.splice(a.indexOf(index),1);
            setSelected([...a]);

            let b = selectedIds;
            b.splice(b.indexOf(item.categoryid),1);
            setSelectedIds([...b]);

            let c = selectedNames;
            c.splice(c.indexOf(item.categoryname),1);
            setSelectedNames([...c]);
        } else {
            setSelected([...selected, index]);
            setSelectedIds([...selectedIds, item.categoryid]);
            setSelectedNames([...selectedNames, item.categoryname]);
        }
        
        // setAnchorEl(null);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleRow = (e, index, row) => {
        setSelectedRowIndex(index);
        setSelectedRow(row);
    }

    const selectSurvey = () => {
        
        if(selectedRow){
            props.data["SurveyName"] = selectedRow["surveyname"];
            props.data["SurveyId"] = selectedRow["surveyid"];
            props.data["CategoryName"] = selectedRow["categoryName"];
            let showWelcomePage = true;

            
            // if(props.previewmode===true && (openingAsApplication.applicationId || formMataData)){
                props.data["SurveyURL"] = `/surveyDesigner/#/preview/${selectedRow["surveyid"]}/${showWelcomePage}`
                // props.data["SurveyURL"] = `/surveyDesigner/#/preview/${selectedRow["surveyid"]}/${showWelcomePage}?rid=S4w7Y8xx3k2UmKSiVdXV00TAVfMFPpa2zt7tI~uKDOA_&preview=N&pid=${pid}&enc=N`
            // }
            // else{
            //     props.data["SurveyURL"] = `/surveyDesigner/#/preview/${selectedRow["surveyid"]}/${showWelcomePage}?rid=S4w7Y8xx3k2UmKSiVdXV00TAVfMFPpa2zt7tI~uKDOA_&preview=Y`
            // }
        }

        // console.log("sur...", window.location.origin, selectedRow, props.data["SurveyURL"], props.data);
        updatedComponentInFormState(props.id || props.data.id , props.data);
        props.handleClose();
    }

  return (
    <div className={classes.paper}
        dir={
            window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA"
            ? "rtl"
            : "ltr"
        }
    >
        <header
            style={{
                boxShadow: "0px -1.5px 5px grey",
                height: "50px",
                display: "flex",
                alignItems: "center",
                justifyContent: "space-between",
                padding: "0px 15px",
            }}
        >
            <span 
                // style={{ font: "font: normal normal 600 14px/19px Open Sans" }}
                className={styles.selectReports}
            >
                {`${t("Select")} ${t("Survey")}`}
            </span>
            <span>
                <ClearIcon style={{ cursor: "pointer" }} onClick={props.handleClose} />
            </span>
        </header>

        <div
            style={{
                width: "751px",
                margin: "auto",
                display: "flex",
                justifyContent: "flex-end",
            }}
        >
            <div
                style={{
                    width: "172px",
                    maxHeight: "28px",
                    border: "1px solid #c4c4c4",
                    borderRadius: "2px",
                    background: "#FFFFFF 0% 0% no-repeat padding-box",
                    margin: "8px 0px",
                }}
            >
                <List style={{ padding: 0 }} >
                    <ListItem
                        style={{ padding: "0px 5px"}}
                        button
                        onClick={handleClickListItem}
                    >
                        <img alt="Filter Icon" src={require('../../../Icons/Filter.svg').default} />
                        <ListItemText 
                            // secondary={selectedNames.length!==0 && selectedNames.join(",")}
                            classes={{
                                root: classes.listItemText,
                            }}
                            // className={styles.selectedList}
                        >
                            {t("Category")}
                        </ListItemText>
                        <KeyboardArrowDownIcon style={{width: "20px",height: "25px"}} />
                    </ListItem>
                </List>

                <Menu
                    id="lock-menu"
                    anchorEl={anchorEl}
                    getContentAnchorEl={null}
                    // keepMounted
                    open={Boolean(anchorEl)}
                    onClose={handleClose}
                    classes={{root: classes.menu}}
                    PaperProps={{
                        style: {  
                            width: '172px',
                            height: "fit-content",
                            marginTop:'2.1%', 
                            maxHeight:"65%", 
                            marginLeft:'-2px',
                            // height:"auto"
                        },
                    }}
                >

                    {categories.map((item,index) => (
                        <div style={{paddingLeft:'13px'}} className={ classes.menuLi}>
                        <MenuItem
                            key={item.categoryname}
                            selected={selected.includes(index)} 
                            // selected={index === selectedIndex}
                            onClick={(event) => handleMenuItemClick(event, index, item)}
                            classes={{root: classes.menuItem}}
                            style={{
                                background: "#FFFFFF 0% 0% no-repeat padding-box",
                            }}
                        >
                            {/* {console.log("selected..", index,selected,selectedIds,selectedNames)} */}
                            <ListItemIcon classes={{root: classes.listItemIcon}} >
                                <Checkbox 
                                    checked={selected.includes(index)} 
                                    // checked={index === selectedIndex}
                                    size="small"
                                    color="primary"
                                    classes={{root: classes.checkbox}}
                                    style={{color:"#0072C6"}}
                                />
                            </ListItemIcon>
                            <div style={{marginLeft:'-19px'}}>
                            <ListItemText classes={{root: classes.listItemText2}} title={item.categoryname}>
                                {item.categoryname} 
                            </ListItemText>
                            </div>
                            
                        </MenuItem>
                        </div>
                    ))}
                
                </Menu>
            </div>

            <div
                style={{
                    display: "flex",
                    justifyContent: "space-between",
                    width: "200px",
                    maxHeight: "28px",
                    border: "1px solid #c4c4c4",
                    margin: "8px 12px",
                    paddingLeft: "8px",
                }}
            >
                <InputBase
                    inputProps={{"aria-label":"Control Value2"}}
                    classes={{root: classes.inputBase, input: classes.input2}}
                    placeholder={t("Search")}
                    value={filter}
                    id="searchInput"
                    onChange={(e) => handleSearch(e)}
                    onKeyDown ={(e) => handleSearch(e)}
                />
                <IconButton classes={{root: classes.searchIcon}} >
                    <span id="searchBtn" className={classes.searchIconSpan} onClick={()=>searchWord()}><SearchIcon /></span>
                </IconButton>
            </div>
            
        </div>

       
        <TableContainer
            classes={{ root: classes.tableContainer }}
        >
            <Table
                stickyHeader
                classes={{ root: classes.table }}
                size="small"
            >
                <TableHead classes={{ root: classes.tableHead }}>
                    <TableRow classes={{ root: classes.tableHeadRow }}>
                        {column.map((col, index) => (
                            <>
                            <TableCell
                                key={index}
                                classes={{ root: classes.tableHeadCell }}
                            >
                                {t(col)}
                            </TableCell>
                            </>
                        ))}
                    </TableRow>
                </TableHead>
                <TableBody>
                    {empty === false ? (
                    activeArray &&
                    activeArray.map((row, index) => {
                        return (
                            <TableRow
                                hover
                                key={index}
                                classes={{ root: index === selectedRowIndex ? classes.activeBodyRow : classes.tableBodyRow }}
                                onClick = {(e)=>handleRow(e, index, row)}
                            >
                                <TableCell
                                    classes={{ root: classes.tableBodyCell1 }}
                                >
                                    {row.surveyname}
                                </TableCell>
                                <TableCell
                                    classes={{ root: classes.tableBodyCell2 }}
                                >
                                    {row.categoryName}
                                </TableCell>
                            </TableRow>
                        );
                    })
                    ) : (
                    <TableRow classes={{ root: classes.tableRow }}>
                        <TableCell classes={{ root: classes.tableBodyCell }}>
                            {t("No Records Found")}
                        </TableCell>
                    </TableRow>
                    )}
                </TableBody>
            </Table>
        </TableContainer>
           
        <footer
            className={classes.foot }
        >
            <div
                style={{
                width: " 160px",
                float: (window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA") ? "left" : "right",
                marginTop: "11px",
                display: "flex",
                justifyContent: "space-evenly",
                }}
            >
                <Button
                disableFocusRipple
                disableRipple
                tabIndex={0} className={`formBuilder-MuiButton-outlined`}
                    variant="outlined"
                    id="surveyButtons"
                    style={{
                        backgroundColor: "#ffffff",
                        width: "54px",
                        height: "28px",
                        fontSize: "12px",
                        borderRadius:"2px",
                        textTransform:"none"
                    }}
                    onClick={props.handleClose}
                >
                    {t("Cancel")}
                </Button>
                <Button
                disableFocusRipple
                disableRipple
                tabIndex={0} className={`formBuilder-MuiButton-outlined`}
                    variant="contained"
                    id="surveyButtons"
                    style={{
                        backgroundColor: "#0072c6",
                        color: "white",
                        width: "43px",
                        height: "28px",
                        fontSize: "12px",
                        borderRadius:"2px",
                        textTransform:"none"
                    }}
                    onClick={()=>selectSurvey()}
                >
                    {t("Select")}
                </Button>
            </div>
        </footer>
    </div>
  );
};

export default SurveyModal;
