import React,{useState, useContext, useEffect,useRef} from 'react'
import {Form} from 'react-bootstrap'
import styles from '../../UI/css/General.module.css'
import ElemDeleteControl from "../BasicFormControls/ElemDeleteControlWrapper"
import styles2 from '../css/COLUMN.module.css'
import QuickTools from '../../ReusableComponents/QuickTools/QuickTools';
import {triggerEventCalls} from '../../ReusableComponents/Events/EventExecutionLogic';
import { getSuggestedId } from "../../components/IFormDesignerLayout/Logic/UserIdLogic";
import CustomizedAlert from '../../ReusableComponents/Alert/CustomizedAlert';
import Modal from "@material-ui/core/Modal";
import SurveyModal from "../BasicFormControls/Survey/SurveyModal";
import EditableLabel from '../../ReusableComponents/EditableLabel/EditableLabel';
import { GetState, GetStateFromData } from '../../components/CustomHooks/CustomHooks'
import { handleDisplayProperty, encrypt} from '../../components/IFormDesignerLayout/IFormDesignerLogic'
import { useTranslation } from 'react-i18next'
import { cloneDeep } from 'lodash-es';

function IFRAME(props) {  
    
    const isSubformInPage = props?.isSubformInPage || false;
    const {t}= useTranslation();    
    const translationStyles1=window?.depsatkur_configs?.selectedLanguage === "ar" || window?.depsatkur_configs?.selectedLanguage === "ar-SA";
    const contexts = GetStateFromData(props.containerId);
    const {tempCanWidth, controlSelected}  = GetState("tempCanWidth","controlSelected");
    const reduxContexts=contexts;
    const {IdPrecedence , deleteControl, openQuickToolFor, openingAsApplication, formMataData,  device, formState2, themeOpen, themeResetDone,themeResetValDone }=contexts.contexts;
    const data = props.data;
    const [fullScreenMode, setFullScreenMode] = useState(false);
    const actualControlId = props.data.id;
    let mappingId = props.data["style"] && props.data["style"]["MappingId"];
    let userDefinedId = props.data["style"] && props.data["style"]["CustomId"];
    let suggestedId = getSuggestedId(actualControlId, mappingId, userDefinedId, IdPrecedence);
    const themeData = formState2.theme;
    const controlThemeData = themeData?.['LabelStyle'];

    const iframeThemeStyle = useRef({
        'fontSize': '14px',
        'color': '#000000',
        'fontFamily': 'Open Sans',
        'fontWeight': '700',
        'fontStyle': 'bold',
        'background': '#FFFFFF',
        'display': 'flex',
        'lineHeight': '14px',
        'justifyContent': 'flex-start',
        'marginBottom':"8px",
        'whiteSpace':"break-spaces"
    })

    const defaultBuffer2 = {
        'fontSize': '14px',
        'color': '#000000',
        'fontFamily': 'Open Sans',
        'fontWeight': '700',
        'fontStyle': 'bold',
        'background': '#FFFFFF',
        'display': 'flex',
        'lineHeight': '14px',
        'justifyContent': 'flex-start',
    };

    const [cloneThemeStyle, setCloneThemeStyle] = useState();
    const [rerenderState, setReRenderState] = useState(false);

    const setIframeStyle = (newStyle) => {
        const prevState = JSON.stringify(iframeThemeStyle.current);
        iframeThemeStyle.current = {...iframeThemeStyle.current, ...newStyle}
        if(!props.previewmode) {
            if(prevState !== JSON.stringify(iframeThemeStyle.current)) {
              setReRenderState(!rerenderState);
            }
          }
    }
    
    useEffect(() => {
        if(themeOpen === true || themeOpen === "true") {
          setCloneThemeStyle(cloneDeep((iframeThemeStyle.current)));
        }
    },[themeOpen])
    
    useEffect(() => {
        if(themeResetDone === true || themeResetDone === "true") {
          Object.assign(iframeThemeStyle.current, cloneThemeStyle);
        }
    },[themeResetValDone]);

    useEffect(() => {
        if(themeData?.['BasicTheme']?.['FontFamily'] && (!controlThemeData?.['FontFamily'] || controlThemeData["FontFamily"] === "Select")){
            setIframeStyle({'fontFamily': themeData['BasicTheme']['FontFamily']})
        }
        if((themeData?.['BasicTheme']?.['FontFamily'] === "" || themeData?.['BasicTheme']?.['FontFamily'] === "Select") && (!controlThemeData["FontFamily"] || controlThemeData["FontFamily"] === 'Select')) {
            setIframeStyle({'fontFamily': defaultBuffer2.fontFamily});
        }
        if(themeData?.['BasicTheme']?.['FontSize'] && (!controlThemeData?.['FontSize'] || controlThemeData?.["FontSize"] === "Select")){
            setIframeStyle({'fontSize': themeData['BasicTheme']['FontSize']})
            setIframeStyle({'lineHeight': themeData['BasicTheme']['FontSize']})
        }
        if (themeData?.["BasicTheme"]?.["ActiveTextColor"] && !(controlThemeData?.["FontColor"])) {
            setIframeStyle({'color': themeData?.["BasicTheme"]?.["ActiveTextColor"]});
          }
      },[themeData?.['BasicTheme']]);

    useEffect(() => {
        if(controlThemeData && controlThemeData['FontColor'])
            setIframeStyle({'color': controlThemeData['FontColor']});
        if(controlThemeData && controlThemeData['BackgroundColor'])
            setIframeStyle({'background': controlThemeData['BackgroundColor']})
        if(controlThemeData && controlThemeData['FontFamily'])
            setIframeStyle({'fontFamily': controlThemeData['FontFamily']})
        if(controlThemeData && (controlThemeData["FontFamily"] === "" || controlThemeData["FontFamily"] === "Select"))
            setIframeStyle({'fontFamily': themeData?.['BasicTheme']?.['FontFamily']  && themeData?.['BasicTheme']?.['FontFamily'] !== 'Select' ? themeData['BasicTheme']['FontFamily'] : defaultBuffer2.fontFamily})
        if(controlThemeData && controlThemeData['FontSize']) {
            setIframeStyle({'fontSize': controlThemeData['FontSize']})
            setIframeStyle({'lineHeight': controlThemeData['FontSize']})
        }
        if(controlThemeData && (controlThemeData['FontSize'] === '' || controlThemeData['FontSize'] === 'Select')) {
            setIframeStyle({'fontSize': themeData?.['BasicTheme']?.['FontSize'] && themeData?.['BasicTheme']?.['FontSize'] !== 'Select' ? themeData['BasicTheme']['FontSize'] : controlThemeData['FontSize']})
            setIframeStyle({'lineHeight': themeData?.['BasicTheme']?.['FontSize'] && themeData?.['BasicTheme']?.['FontSize'] !== 'Select' ? themeData?.['BasicTheme']?.['FontSize'] : controlThemeData['FontSize']})
        }
        if(controlThemeData && controlThemeData['LabelAlignment']) {
            if(controlThemeData['LabelAlignment'] === 'Center')
                setIframeStyle({'justifyContent': 'center'});
            else if(controlThemeData['LabelAlignment'] === 'Left')
                setIframeStyle({'justifyContent': 'flex-start'});
            else if(controlThemeData['LabelAlignment'] === 'Right') 
                setIframeStyle({'justifyContent': 'flex-end'})
        }
        if(controlThemeData && controlThemeData['FontWeight']) {
            if(controlThemeData["FontWeight"].toLowerCase() === "italic") {
                setIframeStyle({'fontStyle': "italic"});
                setIframeStyle({'fontWeight': 300});
              }
              else if(controlThemeData["FontWeight"].toLowerCase() ==="bold"){
                setIframeStyle({'fontStyle': "normal"});
                setIframeStyle({'fontWeight': "bold"});
              }
              else if(controlThemeData["FontWeight"].toLowerCase() ==="normal"){
                setIframeStyle({'fontStyle': "normal"});
                setIframeStyle({'fontWeight': 300});
              }
              else if(controlThemeData["FontWeight"].toLowerCase() ==="bold italic"){
                setIframeStyle({'fontStyle': "italic"});
                setIframeStyle({'fontWeight': "bold"});
              }
        }
        if(controlThemeData && (controlThemeData['FontWeight'] === '' || controlThemeData['FontWeight'] === 'Select')){
            setIframeStyle({'fontStyle': defaultBuffer2.fontStyle});
            setIframeStyle({'fontWeight': defaultBuffer2.fontWeight});
        }
    }, [controlThemeData])

    const conditionalQuickToolOpening =
    props.id === openQuickToolFor ? true : false
    
    const [open, setOpen] =  useState(false);
    const [openQuick, setOpenQuick] =  useState(conditionalQuickToolOpening);
    const elementRefrence= useRef(null);

    const [mobileDisplay, setMobileDisplay] = useState(true);
    const [tabDisplay, setTabDisplay] = useState(true);
	
    const startMobile = useRef();
    const endMobile= useRef();
    const startTab= useRef();
    const endTab= useRef();

    useEffect(() => {
        if (formState2 && formState2.style) {
            startMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["StartValue"]);
            endMobile.current = parseInt(formState2.style?.["Breakpoint"]?.["Mobile"]?.["EndValue"]);
            startTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["StartValue"]);
            endTab.current = parseInt(formState2.style?.["Breakpoint"]?.["Tab"]?.["EndValue"]);
        }
    },[formState2.style?.["Breakpoint"], formState2.style?.["Breakpoint"]?.["Mobile"], formState2.style?.["Breakpoint"]?.["Tab"]]); 

    function getServerName(serverTypeId){
        if(serverTypeId==="1"){
            return "JBoss"
        }
        else if(serverTypeId==="2"){
            return "JBossEAP"
        }
        else if(serverTypeId==="3"){
            return "WebLogic"
        }
        else if(serverTypeId==="4"){
            return "WebSphere"
        }
        else if(serverTypeId==="5"){
            return "JTS"
        }
        else{
            return "JBossEAP"
        }
    }

    useEffect(() => {
        if(props?.previewmode)
        handleDisplayProperty(props, device, setMobileDisplay, setTabDisplay, startMobile.current, endMobile.current, startTab.current, endTab.current)
      },[props?.data?.style?.["MobileVisible"], props?.data?.style?.["TabsVisible"], device, endMobile, endTab, startMobile, startTab,tempCanWidth]);

    var source=null;
    var pid= reduxContexts.generalData.processinstanceid || window?.formBuilder_pid_generalData;
    if(props.data.controlName === "Survey" ){
        
        const activeSessionData= window.activeSessionData_formbuilder;
        var rid = JSON.stringify(
            {
                "EmailId":"S4w7Y8xx3k2UmKSiVdXV00TAVfMFPpa2zt7tI~uKDOA_",
                "CabinetName":activeSessionData?.cabinetName || window.activeCabinet_formbuilder || "JBossEAP",
                "ServerName": getServerName(activeSessionData?.sRT) || "",
                "IpAddress":activeSessionData?.appServerIP || window.location.hostname || "appServerIP",
                "Port": activeSessionData?.appServerPort || window.location.port || "appServerPort"
            });
        rid = encrypt(rid);
        source=props.data.SurveyURL;
        
        if(source){
            if(props.previewmode===true && (openingAsApplication.applicationId || formMataData)){
                source = `${source}?rid=${rid}&preview=N&pid=${pid}&enc=N`
            }
            else{
                source = `${source}?rid=S4w7Y8xx3k2UmKSiVdXV00TAVfMFPpa2zt7tI~uKDOA_&preview=Y`
            }
        }
        
    } else {
        if(props.data.style && props.data.style["SourceURL"]!==undefined && props.data.style["SourceURL"]!==null){
            source=props.data.style["SourceURL"];
        }
    }
    
    var Width = props.data.style && props.data.style["Width"];

    if(Width!==undefined && Width!==null && Width!==''){
    } else {
         Width="100%";
    }
    var Height =  props.data.style && props.data.style["Height"];   
    if(Height !==undefined && Height !==null && Height !==''){

    } else {
         Height="300px";
    }
    useEffect(()=>{
        if(props.previewmode){
            var e={
                "type":"load"
            }
            if(props.data && props.data.event && props.data.event.Events && props.data.event.Events.Load){
                triggerEventCalls(e, {id:props?.data?.id}, props.data.event, props.data.elem_type ,showAlertConfirmNotifications, reduxContexts);
            }
        }
    },[]);

    const [label,setLabel] = useState();

    const [editLabel, setEditLabel] = useState(false);
    const [defaultLabel, setDefaultLabel] = useState(props?.data?.style?.["Label"] || props?.data?.style?.["Label"] === ''? props?.data?.style?.["Label"] : props?.data?.id);

    useEffect(() => {
        if (
            props.data.style &&
            (props?.data?.style?.["Label"] || props?.data?.style?.["Label"] === "")
        ) {
            setDefaultLabel(props.data.style["Label"]);
        }
    }, [props.data.style["Label"]]);

    const bringEditableLabel = (event) => {
         setEditLabel(true);
    };

    const setLabelHandler = (recievedLabel) => {
        try {
            if (props.data && props?.data?.style && (props?.data?.style?.Label || props?.data?.id)) {
            setDefaultLabel((prevDefaultLabel) => {
              if (prevDefaultLabel === recievedLabel) {
                return prevDefaultLabel;
              } else {
                props.data.style["Label"] = recievedLabel;
                return recievedLabel;
              }
            });
            setEditLabel(false);
          }
        } catch (err) {
          console.error(err);
        }
      };
       

    useEffect(() => {
        if((props.data.style && props.data.style.Label && props.data.style["Label"]!==''))
            setLabel(true);
        else setLabel(false);
    },[props.data]);

    var temp_style = {
        'width':Width,
        'height':`${Height}px`,
        'background':"white",
        'border': "1px solid #d6d6d6"  ,
    };

    var fullScreenModeStyle= { 
        'position' : 'fixed',
        'top' : contexts.isWorkItem && window.location.href.includes("workspacestudio") && window.location.href.includes("workspace")  ? '48px' : 0,
        'right' : 0,
        'bottom' :0,
        'left' : 0,
        'zIndex': 1000
    }
    var fullScreenModeStyleIframe= { 
        'width':Width,
        'height':'100%',
        'background':"white",
        'border': "1px solid #d6d6d6"
    }
    function handleFullScreenMode(arg){
        if(arg=="enterFullScreenMode"){
            setFullScreenMode(true);
        }
        else if(arg=="exitFullScreenMode"){
            setFullScreenMode(false);
        }
    }

    
    const [alertData, setAlertData] = useState({'open':false,
                                                'showConfirm':'showNotification'});

  function showAlertConfirmNotifications(dataTobeSet){
    setAlertData(dataTobeSet);
  }

    var padding_left = props.data.style && props.data.style["SpacingLeft"];
    if(padding_left !==undefined && padding_left !==null && padding_left !==''){

    } else {
        padding_left="0px";
    }

    var padding_right = props.data.style && props.data.style["SpacingRight"];
    if(padding_right !==undefined && padding_right !==null && padding_right !==''){

    } else {
        padding_right="0px";
    }

    var padding_top = props.data.style && props.data.style["SpacingTop"];
    if(padding_top !==undefined && padding_top !==null && padding_top !==''){

    } else {
        padding_top="0px";
    }

    var padding_bottom = props.data.style && props.data.style["SpacingBottom"];
    if(padding_bottom !==undefined && padding_bottom !==null && padding_bottom !==''){

    } else {
        padding_bottom="0px";
    }


    var inputStylePadding={  
    paddingLeft : padding_left,
    paddingRight : padding_right,
    paddingTop : padding_top,
    paddingBottom : padding_bottom,
    display:"block",
    direction: translationStyles1 ? "rtl": "ltr"
    }

    const handleClose = () => {
        setOpen(false);
    };
    const handleOpen = () => {  
        setOpen(true);
    }
    
    

    
    
    if(props.previewmode){
        return (
            <>
            {
            props.data?.style?.Visible === "true" &&
            
            <Form.Group
            className={styles2.formStyle + ' spacingThemeControls '} 
             key={data.id} 
             id={suggestedId+'_group'} 
             control_id={suggestedId+'_group'}
             elem_type='iframe' frameindex={data.frameIndex}  elementindex={data.elementIndex} 
             style={mobileDisplay === false || tabDisplay === false ? {display:"none"} : inputStylePadding}
             >
             <Form.Label  
             style={iframeThemeStyle.current} id={suggestedId+'_label'} control_id={props.id} 
             className={styles2.formStyle}
               frameindex={props.data.frameIndex}  elementindex={props.data.elementIndex}>
                 {props?.data?.style?.["Label"] || props?.data?.style?.["Label"] === ''? props?.data?.style?.["Label"] : props?.data?.id}
             </Form.Label>
                
                <div 
                    type="iframe" 
                    control_id={suggestedId}
                    ref={elementRefrence}  
                    id={props.id} 
                    elem_type='iframe' 
                    frameindex={data.frameIndex} 
                    elementindex={data.elementIndex}  
                    style={
                        props.data && props.data.style && props.data.style.FullScreenView == 'true' && 
                        fullScreenMode == true ? fullScreenModeStyle: {position:"relative"}
                    }
                >
                    <iframe 
                        id={`if_${props.id}`} 
                        src={source}
                        key={fullScreenMode} 
                        style={fullScreenMode == true ? fullScreenModeStyleIframe : temp_style}
                    >

                    </iframe>
                    {props.data && props.data.style && props.data.style.FullScreenView == 'true' && fullScreenMode == false && props.previewmode ?
                        <img src={require('../../Icons/EnterFullScreen.svg').default} alt='View full screen' 
                        className={styles2.enterFullScreenIcon} onClick={()=> handleFullScreenMode("enterFullScreenMode")}/>
                    :
                    props.data && props.data.style && props.data.style.FullScreenView == 'true' && fullScreenMode == true && props.previewmode ?
                        <img src={require('../../Icons/ExitFullScreen.svg').default} alt='Exit full screen' 
                        className={styles2.exitFullScreenIcon} style={contexts.isWorkItem && window.location.href.includes("workspacestudio") && window.location.href.includes("workspace")  ? {top:0}: null} onClick={()=> handleFullScreenMode("exitFullScreenMode")}/>
                    :
                    null
                    }
                    {
                        props.data && props.data.style && props.data.style.FullScreenView == 'true' && fullScreenMode == false && !props.previewmode &&
                        <img src={require('../../Icons/EnterFullScreenDisabled.svg').default} alt='View full screen' 
                        className={styles2.enterFullScreenIcon} />
                    }
                 </div>
            </Form.Group>
            }
            </>
       )
    } else {
        return (
            <>
            <Form.Group  onClick={isSubformInPage ? () => {} : (e)=> props.handleSelected(e,props.id,"n")}
            
            onKeyDown={(e) => props.handleSelected(e)}
            tabIndex="1"
            className={styles2.formStyle + ' spacingThemeControls '} 
            style={inputStylePadding}
            key={data.id}
            id={suggestedId+'_group'} control_id={props.id} elem_type='iframe' frameindex={data.frameIndex}  elementindex={data.elementIndex} >          

             <EditableLabel 
              defaultLabel={
                <Form.Label  
                onDoubleClick={bringEditableLabel}
                style={iframeThemeStyle.current} id={suggestedId+'_label'} control_id={props.id} 
                className={styles2.formStyle}
                frameindex={props.data.frameIndex}  elementindex={props.data.elementIndex}>
                    {props?.data?.style?.["Label"] || props?.data?.style?.["Label"] === ''? props?.data?.style?.["Label"] : props?.data?.id}
                </Form.Label>
             }
              editlabel={editLabel}
              default_value={defaultLabel}
              setLabelHandler={(recievedLabel)=>setLabelHandler(recievedLabel)}
             />

                <div type="iframe" control_id={props.id} id={suggestedId} elem_type='iframe' frameindex={data.frameIndex}  
                elementindex={data.elementIndex} style={{position:"relative"}}>
                    {(props.data.controlName === "Survey") ?
                        (props?.data?.SurveyURL) ?
                        <iframe id={`if_${suggestedId}`} control_id={props.data.id} src={source} style={temp_style} frameBorder="0"></iframe>
                        :
                        <div className={styles.iframeReplacementDivCss+" d-flex justify-content-center align-items-center"} control_id={props.data.id} style={{height:temp_style.height}}>
                            <div className={styles.iframeReplacementTextCss} control_id={props.data.id}>{t("iframSourceUrl")}</div>
                        </div>
                    :
                        (props.data?.style?.SourceURL!==null && props.data?.style?.SourceURL!==undefined && props.data?.style?.SourceURL!=='')?
                        <iframe id={`if_${suggestedId}`} control_id={props.data.id} src={source} style={temp_style} frameBorder="0"></iframe>
                        :
                        <div className={styles.iframeReplacementDivCss+" d-flex justify-content-center align-items-center"} control_id={props.data.id} style={{height:temp_style.height}}>
                            <div control_id={props.data.id} className={styles.iframeReplacementTextCss}>{t("iframSourceUrl")}</div>
                        </div>
                    }
                    {props.data && props.data.style && props.data.style.FullScreenView == 'true' && fullScreenMode == false && props.previewmode ?
                        <img control_id={props.data.id} src={require('../../Icons/EnterFullScreen.svg').default} alt='View full screen' 
                        className={styles2.enterFullScreenIcon} onClick={()=> handleFullScreenMode("enterFullScreenMode")}/>
                    :
                    props.data && props.data.style && props.data.style.FullScreenView == 'true' && fullScreenMode == true && props.previewmode ?
                        <img control_id={props.data.id} src={require('../../Icons/ExitFullScreen.svg').default} alt='Exit full screen' 
                        className={styles2.exitFullScreenIcon} style={contexts.isWorkItem && window.location.href.includes("workspacestudio") && window.location.href.includes("workspace")  ? {top:0}: null} onClick={()=> handleFullScreenMode("exitFullScreenMode")}/>
                    :
                    null
                    }
                    {
                        props.data && props.data.style && props.data.style.FullScreenView == 'true' && fullScreenMode == false && !props.previewmode &&
                        <img src={require('../../Icons/EnterFullScreenDisabled.svg').default} alt='View full screen' 
                        className={styles2.enterFullScreenIcon} />
                    }

                    {
                        props.data.controlName === "Survey" &&
                        <button
                            control_id={props.data.id}
                            type="button"
                            style={{
                                border: "none",
                                background: "none",
                                cursor: "pointer",
                                color: "#0072C6",
                            }}
                            onClick={handleOpen}
                        >
                            {t("Select Survey")}
                        </button>
                    }

                    
                </div> 
                
              {controlSelected?.id === props?.id &&
              <ElemDeleteControl 
                isSubformInPage={isSubformInPage}
              containerId={props.containerId}
                id={props.id} 
                deleteControl={deleteControl} 
                elemType={props.data?.elem_type}
                setDragging={props.setDragging}
                handleReorderStart={props.handleReorderStart}
                handleReorderDrop={props.handleReorderDrop} 
                dragId={props.dragId}
                dragType={'column'}
                data={props.data}
                /> 
              }
            </Form.Group>
            { props.data.controlName === "iframe" && window?.openQuickTools && openQuick &&
                <QuickTools containerId={props.containerId} selectedControl={props.data} open={openQuick} setOpen={setOpenQuick} />
            }
            { props.data.controlName === "Survey" &&
                <Modal open={open} onClose={setOpen}>
                    <SurveyModal
                        containerId={props.containerId}
                        handleClose={handleClose}
                        data={props.data} 
                    />
                </Modal>
            }
            {
              alertData.open === true &&
            <CustomizedAlert alertData={alertData} />
            }

            </>
       )
    }

}

export default IFRAME